
@import url('https://fonts.googleapis.com/css?family=Kanit');
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
	margin-top: 60px;
	font-family: 'Kanit', sans-serif;
}
h1,
h2 {
	font-weight: normal;
}
ul {
	list-style-type: none;
	padding: 0;
}
li {
	display: inline-block;
	margin: 0 10px;
}
a {
	color: #42b983;
}
.chat {
	border: 1px solid #ccc;
	border-radius: 6px;
	padding: 10px;
	margin-bottom: 10px;
	max-height: 500px;
	overflow-y: auto;
	overflow-x: hidden;
	max-width: 75vw;
	overflow-y: auto;
	margin-left: 120px;
}
.name {
	font-size: 15px;
	font-weight: bold;
	margin-bottom: 5px;
}
.message {
	border-radius: 5px !important;
	background: #E0EDFF;
	padding: 5px 12px;
	font-size: 15px;
}
.row {
	margin-bottom: 15px;
}
#message-textarea {
	width: 80%;
	max-width: 100%;
	max-height: 100px;
}
img {
	width: 200px;
	height: 200px;
}



